.header {
  min-height: 72px;
  padding: 9px 0;
  box-shadow: 0 4px 24px rgba(105, 105, 105, 0.1);
}

.header__container {
  display: flex;
  align-items: center;
  line-height: 1.19;
}

.header__btn-order {
  @include mb(mob-only) {
    display: none;
  }
}

.header__burger-icon {
  display: block;

  @include mb(tab) {
    display: none;
  }
}

.header__logo-icon {
  @include mb(tab) {
    margin-right: auto;
  }
}

.header__logo-svg {
  display: block;
}
